@import "./variables/colors";
@import "./variables/typography";

* {
  box-sizing: border-box;
}

/**MAIN STYLES*/
body {
  margin: 0;
  padding: 0;
  top: 0;
  left: 0;
  font-family: $base-font;
}

::selection {
  background-color: $main-color; /* WebKit/Blink Browsers */
}
::-moz-selection {
  background-color: $main-color; /* Gecko Browsers */
}

/**ROW*/
.row {
  padding: 30px;
  max-width: 960px;
  margin: auto;
  margin-top: 20px;
  margin-bottom: 20px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-content: stretch;
  justify-content: center;
}

.App {
  min-height: 100vh;

  display: flex;
  flex-direction: column;
}

/**LAYOUT STYLES*/

/**NAVBAR*/
.Nav {
  width: 100%;
  padding: 10px;
  background-color: $main-color;
  position: relative;
  top: 0;
  z-index: 9999;

  position: sticky;

  transition: 0.5s ease;

  .nav-link-container {
    width: 100%;
    max-width: 960px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    @media screen and (min-width: 500px) {
      flex-direction: row;
    }

    .main-nav-links {
      display: flex;
      flex-direction: column;
      align-items: center;
      display: none;
      height: 0;
      transition: 0.5s ease;

      @media screen and (min-width: 500px) {
        display: flex;
        height: auto;
        flex-direction: row;
      }

      // MOBILE MENU TOGGLE CLASS
      &.toggle {
        height: 100%;
        display: flex;
        flex-direction: column;
      }
    }
  }

  // path{
  //   fill: $white;
  // }

  .nav-link {
    //POSITIONING
    margin: 5px 10px;
    position: relative;

    //FONT SETTINGS
    text-decoration: none;
    color: $white;
    font-size: 1.3rem;
    font-family: $navlink-font;

    &:hover {
      cursor: pointer;
    }

    &:before {
      content: "";
      visibility: hidden;
      position: absolute;
      bottom: -2px;
      left: 0;

      height: 1px;

      width: 100%;
      transform: scaleX(0);
      transition: all 0.2s ease-in-out;
      background-color: $white;
    }

    //animate link underline on hover
    &:hover:before,
    &:active:before,
    &:focus:before {
      visibility: visible;
      transform: scaleX(1);
    }
  }

  .contact-button {
    margin: 20px 10px;
    padding: 10px;
    border: 1px solid $btn-ct-color;
    border-radius: 5px;
    background-color: none;
    transition: all 0.5s ease;

    //FONT SETTINGS
    text-decoration: none;
    color: $white;
    font-size: 1.3rem;
    font-family: $navlink-font;

    &:hover,
    &:focus,
    &:active {
      cursor: pointer;
      background-color: $btn-ct-color;
      opacity: 0.5;
      color: $main-color;
    }

    @media screen and (min-width: 500px) {
      margin-left: auto;
    }
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/**EDUCATION SECTION*/
.education {
  width: 100%;
  display: block;
  margin: 20px 10px;
  padding: 10px;
  border: 1px solid $main-color;
  border-radius: 5px;

  @media screen and (min-width: 960px) {
    max-width: 250px;
  }
}

/**HERO SECTION*/
.Hero {
  width: 100%;
  max-width: 960px;
  margin: auto;
  // background: $hero-color;
  background: none;
  padding: 30px;
  display: flex;
  flex-wrap: wrap-reverse;
  align-items: center;
  justify-content: center;

  h1 {
    // color: $header-color;

    font-family: $base-header-font;
    font-size: 6rem;
    margin: 0;

    @media screen and (min-width: 500px) {
      font-size: 10rem;
    }
  }

  .hero-text {
    width: 100%;
    max-width: 400px;
  }

  .hero-img {
    width: 100%;
    max-width: 400px;

    img {
      width: 100%;
    }
  }

  .social-icon {
    text-decoration: none;
    padding: 5px 10px;
    font-size: 2rem;
    transition: 0.3s ease;
    // change this to match final text color
    color: #000;

    &:hover,
    &:focus,
    &:active {
      cursor: pointer;
      color: $main-color;
    }
  }
}

/**SKILLS SECTION **/
#skills {
  background: url("https://images.unsplash.com/photo-1525905708812-b271b5e3b2f3?ixlib=rb-1.2.1&auto=format&fit=crop&w=1101&q=80");
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
}

.skills {
  width: 100%;
  max-width: 800px;
  padding: 10px;

  @media screen and (min-width: 960px) {
    max-width: 300px;
  }

  .bar {
    width: 100%;
    position: relative;
    display: block;
    height: 10px;
    background-color: none;
    border: 1px solid $white;
    overflow: hidden;

    .bar-content {
      display: block;
      position: absolute;
      height: 8px;
      top: 0;
      left: 0;
      background-color: $white;
    }
  }
}

/**EXPERIENCE SECTION*/
.experience {
  width: 100%;
}

/**CONTACT SECTION*/

.Contact {
  width: 100%;
  padding: 5px;
  input,
  textarea {
    margin-top: 10px;
    max-width: 100%;
    min-width: 100%;
    max-height: 1000px;
    width: 100%;
    padding: 10px;
    background: none;
    border: 1px solid $main-color;
    font-family: $base-font;
    font-size: 1rem;

    &:active,
    &:focus {
      background-color: rgba(153, 204, 204, 0.5);
    }
  }
  textarea {
    min-height: 200px;
  }
  button {
    margin-top: 10px;
    padding: 10px;
    background: none;
    border: 1px solid $main-color;
    font-family: $base-font;
    font-size: 1rem;

    transition: 0.5s ease;

    &:focus,
    &:hover,
    &:active {
      cursor: pointer;
      background-color: $main-color;
      color: $white;
    }
  }
}

/**FOOTER*/
.Footer {
  clip-path: polygon(0 0, 100% calc(0% + 6vw), 100% 100%, 0 100%);

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  position: relative;
  padding-top: 6vw;
  padding-bottom: 4vw;
  padding-left: 20px;
  padding-right: 20px;
  width: 100%;
  bottom: 0;
  left: 0;
  background-color: $main-color;
}
